$primary-color: #27ae60;
$space-m: 16px;
$color-temp1: #ea907a;
$color-temp2: #fbc687;
$color-temp3: #f4f7c5;
$color-temp4: #aacdbe;
$nicegray: #303030;

.mobile-menu {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 $space-m;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  z-index: 9999999;

  &__btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 35px;
    height: 30px;
    cursor: pointer;
    transition: 0.4s;
    z-index: 1050;
  }

  &__icon {
    display: block;
    position: relative;
    background: $nicegray;
    width: 90%;
    height: 4px;
    transition: 0.4s;
    border-radius: 2px;

    &::after,
    &::before {
      content: "";
      display: block;
      position: absolute;
      background: $nicegray;
      width: 100%;
      height: 4px;
      transition: 0.4s;
      border-radius: 2px;
    }
    &::after {
      top: 8px;
    }
    &::before {
      top: -8px;
    }
  }

  &__container {
    position: fixed;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 0;
    right: 0;
    z-index: 999;
    width: 0;
    opacity: 1;
    transition: 0.5s;
    transition-delay: 0.2s;
    overflow: hidden;
    background-color: $color-temp2;
  }
  &__logocontainer {
    width: 300px;
    padding-top: 30px;
    padding-bottom: 10px;

    background-color: $color-temp3;
    box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1),
      0 0 0 2px rgb(255, 255, 255), 0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
  }
  &__logosolo {
    height: 80px;
  }
  &__textlogo {
    height: 80px;
  }
  &__list {
    transition: 0.5s;
    transition-delay: 0.5s;
    list-style: none;
    padding-left: 0;
    margin-top: 10px;
    margin-bottom: 100px;
  }

  &__item {
    width: 170px;
    position: relative;
    box-shadow: 0 16px 8px -8px $color-temp1;
    font-size: 15px;
    padding-bottom: 0px;
    margin-top: 15px;
  }

  &__link {
    width: 100%;
    text-decoration: none;
    color: rgb(6, 0, 31);
  }

  &__checkbox {
    display: none;

    &:checked ~ {
      .mobile-menu {
        &__nav {
          opacity: 1;
          transition-delay: 0s;
        }

        &__container {
          width: 50%;
          transition-delay: 0s;
        }

        &__btn {
          .mobile-menu__icon {
            background: transparent;

            &::before,
            &::after {
              top: 0;
            }

            &::after {
              transform: rotate(-45deg);
              -webkit-transform: rotate(-45deg);
            }

            &::before {
              transform: rotate(45deg);
              -webkit-transform: rotate(45deg);
            }
          }
        }
      }
    }
  }
}
